import { usePlanContext } from "../context/PlanContext";

const Plans = ({ name }) => {
  const { plans, planSelected } = usePlanContext();

  return (
    <div>
      <pre>
        {name} - plans: {JSON.stringify(plans)}
      </pre>
      <pre>
        {name} - plan selected: {JSON.stringify(planSelected)}
      </pre>
    </div>
  );
};

export default Plans;
